.root {
    position: absolute;
    width: 200px;
    top: 0;
    left: 0;
    z-index: 600;
    background: #fff;
    border: none;
    border-radius: 4px;
    pointer-events: none;
    overflow: hidden;
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
}
.root img {
    display: block;
    width: 100%;
}
