.root {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 700;
    border: none;
    border-radius: 4px;
    padding: 15px 30px;
    pointer-events: none;

    background: #000000a6;
    border: 0;
    color: white;
    box-shadow: 0 1px 3px #26262680;
}
