.trackPanel {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 500;
    overflow: hidden;
    pointer-events: all;
    background: black;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container {
    width: 960px;
    display: flex;
    flex-direction: column;
}


.image {
    width: 100vw;
    height: 56.25vw;
    position: relative;
}

.image img {
    z-index: 1;
    width: 100vw;
    height: 56.25vw;
    top: 0;
    left: 0;
}

.frameTransitionEnter {
    z-index: 1;
    width: 100vw;
    height: 56.25vw;
    top: 0;
    left: 0;
}

.frameTransitionExit {
    position: absolute;
    width: 100vw;
    height: 56.25vw;
    top: 0;
    left: 0;
    z-index: 2;
    animation: transitionExit 0.5s ease-out;
}

@keyframes transitionExit {
    0% {
        opacity: 1.0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 0.0;
    }
}

.controls {
    left: calc(20vw + 16px);
    position: absolute;
    z-index: 600;
    top: 8px;
}


.info {
    left: 8px;
    top: calc(12vw + 56px);
    position: absolute;
    z-index: 600;
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.75);
    color: white;
    margin: 0;
    width: 20vw;
    padding: 8px 8px;
    box-sizing: border-box;
    font-family: Arial;
    font-size: 13px;
}

.info a,
.info a:active,
.info a:visited {
  color: white;
}

.info dl {
    margin: 0;
}

.info dt {
    opacity: 0.85;
    font-size: 0.75vw;
}

.info dd {
    margin-inline: 0;
    margin-bottom: 8px;
    font-size: 0.8vw;
}

.buttons {
    display: flex;
    justify-content: center;
}

.similarPanel > button,
.buttons > button {
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
    height: 32px;
    border-radius: 4px;
    border: 0;
    background: rgba(0, 0, 0, 0.65);
    color: white;
    margin-right: 8px;
    cursor: pointer;
}

.similarPanel {
    display: flex;
    flex-direction: column;
    z-index: 600;
    position: absolute;
    left: 0px;
    bottom: 8px;
    align-items: flex-start;

    transition-property: transform;
    transition-timing-function: ease-out;
    transition-duration: 0.25s;
}

.hidden {
    transform: translateY(9vw) translateY(8px);
}

.similarPanel > button {
    margin: 8px;
}


.similar {
    display: flex;
}

.similarTrack {
    position: relative;
    width: 16vw;
    height: 9vw;
    margin-left: 10px;
    box-sizing: border-box;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
}

.similarTrack img {
    max-width: 100%;
    max-height: 100%;
    cursor: pointer;
}

.similarTrack label {
    position: absolute;
    left: 4px;
    top: 4px;
    z-index: 1;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    font-size: 14px;
}

