.map {
    margin: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 600;

    transition-property: height, width, left, bottom;
    transition-timing-function: ease-out;
    transition-duration: 0.25s;

}

.map.minimized {
    width: 20vw;
    height: 12vw;
    left: 8px;
    top: 48px;
    border-radius: 4px;
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
}

.project {
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
    height: 32px;
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.65);
    color: white;
    padding: 8px;
    border: 0;
    pointer-events: all;
    position: absolute;
    left: 8px;
    top: 8px;
    z-index: 610;
    width: 20vw;

}

.controls {
    left: calc(20vw + 16px);
    position: absolute;
    z-index: 600;
    top: 8px;
}

.buttons {
    display: flex;
    justify-content: center;
}

.buttons > button {
    box-shadow: 0 1px 3px 0 rgb(38 38 38 / 50%);
    height: 32px;
    border-radius: 4px;
    border: 0;
    background: rgba(0, 0, 0, 0.65);
    color: white;
    margin-right: 8px;
    cursor: pointer;
}